// vuex状态管理
import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
// 全局注册
Vue.use(Vuex);

// 创建Store对象
const store = new Vuex.Store({
  // 状态数据
  state: {
    // 主体
    topics: [{
        name: 'all',
        desc: '全部'
      },
      {
        name: 'good',
        desc: '精华'
      },
      {
        name: 'share',
        desc: '分享'
      },
      {
        name: 'ask',
        desc: '问答'
      },
      {
        name: 'job',
        desc: '招聘'
      },
      {
        name: 'dev',
        desc: '测试'
      }
    ],
    // 是否显示更多菜单
    isShowMoreMenus: false,
    // 登录用户名
    loginname: '',
    // 登录用户的accesstoken
    accesstoken: '',
    // 登录用户id
    author_id: '',
    isLogined: false,
    // 登录时更多菜单
    loginedMoreMenus: {
      create: '发表新话题',
      center: '个人中心',
      msg: '消息通知',
      logout: '登出'
    },
    // 未登录时更多菜单
    unLoginedMoreMenus: {
      login: '登录',
    }
  },
  // state的状态属性
  getters: {
    moreMenus(state) {
      if (state.isLogined) {
        return state.loginedMoreMenus;
      }
      return state.unLoginedMoreMenus;
    },
    getTopicDescByTab(state) {
      // 需要返回一个函数结构 因为有参数
      return function (tab) {
        for (var i = 0; i < state.topics.length; i++) {
          if (state.topics[i].name == tab) {
            return state.topics[i].desc;
          }
        }
      }
    }
  },
  mutations,
  actions
});

export default store;



